:root {
  --purple: #405cf5;
  --bluegreen: #20cdbb;
}

h1 {
  font-size: 3rem;
  color: white;
}

h2 {
  font-size: 2.5rem;
}

p {
  font-size: 1.5rem;
}

button {
  appearance: button;
  backface-visibility: hidden;
  background-color: var(--purple);
  border-radius: 30px;
  border-width: 0;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: -apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Ubuntu,sans-serif;
  font-size: 100%;
  height: 44px;
  line-height: 1.15;
  margin: 12px 0 0;
  outline: none;
  overflow: hidden;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-transform: none;
  transform: translateZ(0);
  transition: all .2s,box-shadow .08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  /* width: 100%; */
}

button:disabled {
  cursor: default;
}

button:focus {
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  background-color: var(--bluegreen);
}

.button-holder {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 65vw;
  background-color: rgb(250, 250, 250);
  padding: 4rem;
  border-radius: 2.5rem;
  color:rgb(43, 43, 43)
}

.footer {
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-weight: 400;
}

.footer p {
  font-size: 1rem;
  color: white;
}

.footer a {
  color: rgb(235, 251, 6);
  font-weight: 600;
}

.subtext {
  font-size: 1.25rem;
  font-weight: 300;
  font-style: italic;
  margin-top: -0.5rem;
  margin-bottom: 2rem;
}